import React, { Component } from 'react';
import { Link } from 'react-router';
import { ComponentLeader, Loader } from '../widgets';

class Guide extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div style={{paddingTop: '57px'}}>
                <ComponentLeader title="Services"/>
                <div className="services-container">
                    <Link to="/services/shortTerm">
                        <div className="services-card short-term-card">
                            <i className="fa fa-hand-peace-o" aria-hidden="true"></i>                    
                            <div className="service-name">
                                <h2>Short-Term Service</h2>
                            </div>
                        </div>  
                    </Link>
                    <Link to="/services/longTerm">
                        <div className="services-card long-term-card">
                            <i className="fa fa-handshake-o" aria-hidden="true"></i>                    
                            <div className="service-name">
                                <h2>Long-Term Service</h2>                        
                            </div>
                        </div>                    
                    </Link>
                    
                </div>
            </div>
        );
    }
}

export default Guide;


